@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Chakra+Petch:ital,wght@0,600;1,400;1,500;1,700&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Chackra Petch', sans-serif;
}

body{
    background-color: #e2dada;
}
h1{
    padding:2rem;
}
.App {
  text-align: center;
}

.App{
  background-color: rgba(12, 12, 12, 0.042);
}
.fa-shopping-cart{height:4em;color:#fdfdff;background-color:transparent;margin-top:2em}.fa-shopping-cart:hover{color:#001eff;text-shadow:-6px 5px 20px #fdfdff}.openCart{position:absolute;right:0px;background-color:#fdfdff;top:95px;display:flex;flex-direction:column;color:#0c0127}.openCart button{display:flex;justify-content:center;width:80%;height:50px;align-items:center;color:#020202;border:none;background-color:#efefef;font-weight:600;font-size:20px;text-decoration:none;text-align:center;border-radius:20px;margin:0.5rem auto}.cartDetail{display:flex;flex-direction:column;justify-content:space-evenly;border:2px solid #001eff;padding:10px;margin:10px;width:300px;text-align:center}.cartDetail p{line-height:1.8em}.cartDetail button{width:200px;color:#0c0127;background-color:#efefef;border:none;padding:5px;margin:0.5rem auto;font-weight:600;font-size:20px;border-radius:20px}

nav{display:flex;text-align:center;justify-content:space-evenly;background-image:url(/macintyresj/ecommerceprojectTiendaSinNombre/static/media/fondo.9bb358ed.png);background-position:center;color:#fdfdff;height:100px}nav li{display:inline-block;list-style-type:none;margin-top:2em}nav li a{text-decoration:none;color:#fdfdff;padding:2em}a:hover{color:#001eff}

h1{text-transform:uppercase;color:black;text-align:center;font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif}.listadoProductos{display:flex;flex-flow:wrap}.swal-overlay{background-color:rgba(0,0,0,0.5);-webkit-backdrop-filter:blur(4px);backdrop-filter:blur(4px)}.swal-modal{background-color:#fafafa;color:black}.swal-button{background-color:black;color:#fafafa}

.itemDetalle a{color:#0c0127;text-decoration:none;vertical-align:sub;text-transform:uppercase}.itemDetalle:hover{color:#e9e6e0;background-color:#001eff4f;border:#001eff}.itemDetalle{margin-top:15px;border:none;border-radius:40px;height:40px;background-color:#e9e6e0}.price{font-weight:700;font-size:22px;margin:20px}hr{height:2px;border:0px solid #001eff;background-color:#001eff;color:#001eff;margin:10px}

.itemList{border:1px solid #001eff;box-shadow:7px 1px 10px #001eff3a;background-color:white;flex-direction:column;align-items:center;padding:10px;margin:20px;width:300px;text-align:center}.itemList img{height:150px}.bloque{display:flex;flex-wrap:wrap;justify-content:center}

.itemDetail{border:1px solid #1a1a1b34;box-shadow:2px 2px 2px 1px #37007536;display:flex;padding:10px;margin:30px auto;width:800px;background-color:#fcfcfc}.itemDetails{width:300px;text-align:center;margin:auto}.itemDetail__img{max-height:400px}.agregar{display:block;width:90%;color:black;background-color:whitesmoke;border:none;padding:10px;font-weight:600;font-size:20px}.itemDetail__precio{font-size:30px;font-weight:500}.itemDetail a{display:flex;justify-content:center;width:100%;color:black;border:none;border-radius:20px;margin:5px;padding:10px;font-weight:600;text-decoration:none}.texto{display:flex;flex-direction:column;justify-content:center;margin:auto;padding-left:20px}.texto p{padding-bottom:1em}.comprar button{border:none;border-radius:40px}.comprar button:hover{background-color:#001eff4d;font-size:0.8rem;border:#001eff}

.product{display:inline-block;margin:20px;padding:10px;text-align:center}.product button{width:50px;height:50px;color:#0d0d0e;background-color:#f1f1f3;border-radius:40px;border:none;margin-top:10px;padding:10px}.product span{margin:2px 5px;padding:0 3px}.product .addButton{display:block;width:100%;border:none;padding:8px}.product button:hover{background-color:#001eff4d;font-size:0.8rem}

.contactoContainer{display:flex;flex-direction:column;align-items:center;letter-spacing:2px}.contactForm{margin:10px;width:500px;font-size:20px}.contactForm input{width:100%;height:30px;border:none;border-radius:15px}.contactForm input:active{background-color:#0c0127}.contactForm textarea{width:100%;height:120px;border:none;border-radius:20px}.contactForm button{color:#020202;background-color:#e9e6e0;padding:14px 25px;border:none;margin:10px auto;font-weight:600;font-size:18px;display:block;border-radius:20px}.mensaje{text-align:center;margin:50px;height:700px}

.cartDetalle{display:flex;padding:3%;text-align:center}.cartDetalle p{padding:0.5rem;margin-right:2rem}.cartDetalle button{width:40px;height:40px;color:#0c0127;border:none;margin:auto;font-weight:600;font-size:20px;border-radius:50px}.cartDetalle button:hover{background-color:#001eff4d;font-size:0.8rem;border:#001eff}.cart{display:flex;flex-direction:column;align-items:center;margin:20px;min-height:700px}.cart h1{text-align:center}.carrito_vacio{display:flex;width:200px;justify-content:center;align-items:center;height:50px;color:#0c0127;border:none;margin-bottom:20px;font-weight:600;font-size:20px;border-radius:20px}.cartContainer{text-align:center;display:flex;flex-direction:column;width:80%;justify-content:space-evenly;background-color:#fdfdff}.carritoVacioMensaje{font-size:30px;margin:2rem;padding:1rem}.carritoVacioBoton{display:block;color:#020202;border:none;margin:10px auto;font-weight:500;font-size:20px;text-decoration:none;text-align:center;padding:10px;border-radius:20px;background-color:#fdfdff}.carritoVacioBoton:hover{border:#001eff;text-shadow:2px 2px 5px #001eff27}.total p{text-align:right;margin-right:2rem}.checkoutCart{display:flex;width:200px;justify-content:center;height:50px;align-items:center;color:#020202;border:none;background-color:#efefef;font-weight:600;font-size:20px;text-decoration:none;text-align:center;border-radius:20px}.buttons{display:flex;margin:10px;font-size:20px}

.orderContainer{display:flex;flex-direction:column;align-items:center;letter-spacing:2px}.formCheckout{margin:10px;width:500px;font-size:20px}.formCheckout input{width:100%;height:30px;border:none;border-radius:15px}.formCheckout input:active{background-color:#0c0127}.formCheckout button{color:#020202;background-color:#e9e6e0;padding:14px 25px;border:none;margin:10px auto;font-weight:600;font-size:18px;display:block;border-radius:20px}.order{text-align:center;margin:50px;height:700px}

.footer{display:flex;justify-content:flex-end;background-color:#0d0d0e;color:#001eff;text-shadow:5px 5px 20px;padding:2em;margin-top:2rem;margin-bottom:0}

